@media (min-width: 1025px) and (max-width: 2560px) {
  
	.slide-container{
        margin: 0 auto;
        max-width: 1280px;
        width: 70%;
	}
	
  }

  @media  (min-width: 1023px) and (max-width: 1024px) {
  
	.slide-container{
        overflow: hidden;
        margin: 0 auto;
        width: 86%;
	}
	
  }

